/*	dsIDX Styles
--------------------------------------------------------*/
#dsidx.dsidx-results{
  a{
    text-decoration: none;
    &:hover{
      text-decoration: none;
    }
  }
  .dsidx-paging-control{
    float: left;
    text-align: left;
    font-size: 12px;
  }
  .dsidx-sorting-control{
    font-size: 12px;
    text-align: left;
    form select{
      @include border-radius(0);
      border-color: #dedede;
      margin-bottom: 2px;
      margin-left: 5px;
    }
  }
  #dsidx-map-control{
    clear: both;
    margin-top: 10px;
  }
  #dsidx-map{
    border: none;
  }
}

#dsidx-disclaimer{
  clear: both;
  font-size: 12px;
  line-height: 20px;
  margin-top: 10px;
  // display: none; /* disable this line to show IDX Disclaimer */
}


/* dsIDXpress Listing Styles
--------------------------------------------------------*/
#dsidx-listings{
  margin: 0;
  li{
    display: none;
  }
  li.dsidx-listing{
    display: block;
    margin: 0 0 30px;
    border: 1px solid #dedede;
    padding: 20px;
    width: auto;
  }
  .dsidx-media .dsidx-photo{
    border: none;
  }
  .dsidx-primary-data{
    background-color: #4dc7ec;
    color: #fff;
    padding-top: 8px;
    padding-bottom: 8px;
    width: 62%;
  }
  .dsidx-address{
    display: inline;
    margin-left: 10px;
    a{
      font-family: "Lato", Helvetica, Arial, sans-serif;
      font-size: 18px;
      font-weight: normal;
      color: #fff;
      &:hover{
        color: #fff;
      }
    }
  }
  .dsidx-price{
    font-family: "Lato", Helvetica, Arial, sans-serif;
    color: #fff;
    font-size: 18px;
    font-weight: normal;
    padding-left: 6px;
    display: inline;
    float: left;
  }
  .dsidx-secondary-data {
    width: auto;
    div{
      background: url(../images/icon-arrow-small.png) no-repeat 4px 6px;
      padding-left: 20px;
      float: left;
      margin: 5px 3px;
      width: 40%;
      font-family: Helvetica, Arial, sans-serif;
      font-size: 13px;
      line-height: 23px;
    }
  }
}

.dsidx-results{
  li.dsidx-prop-summary{
    padding: 15px 0 15px 15px;
    margin-bottom: 30px;
    border:1px solid #dedede;
    .dsidx-prop-title{
      background-color: #4dc7ec;
      color: #fff;
      padding: 8px 10px;
      font-family: "Roboto", Helvetica, Arial, sans-serif;
      font-weight: normal;
      text-transform: capitalize;
      margin: 0;
      font-size: 18px;
      b{
        font-weight: normal;
      }
      a{
        color: #fff;
        &:hover{
          text-decoration: underline;
        }
      }
    }
    .dsidx-prop-features{
      div{
        background: url(../images/icon-arrow-small.png) no-repeat 4px 6px;
        padding-left: 20px;
        float: left;
        margin: 5px 3px;
        width: 19%;
        font-family: Helvetica, Arial, sans-serif;
        font-size: 13px;
        line-height: 23px;
      }
    }
  }
}


/* dsIDXpress Details Page Styles
--------------------------------------------------------*/
#dsidx.dsidx-details{
  #dsidx-actions{
    margin: 0 0 15px;
    font-family: "Roboto",Helvetica,Arial,sans-serif;
    font-weight: normal;
  }
  #dsidx-description{
    background: #fff;
    border: 1px solid #dedede;
    padding: 15px;
    color: #8b9293;
    font-size: 13px;
    line-height: 23px;
  }

  #dsidx-secondary-data,
  .dsidx-supplemental-data{
    border: none;
    th,
    td{
      background: #fbfbfb;
      color: #636464;
      font-size: 13px;
      line-height: 23px;
      border: 1px solid #dedede;
    }
    tr:hover{
      th,td{
        background: #f1f1f1;
      }
    }
  }
  h3{
    margin-top: 20px;
  }
  #dsidx-map{
    border: none;
  }
  #dsidx-contact-form{
    background: #fbfbfb;
    border: 1px solid #dedede;
    padding: 15px;
  }
  .dsidx-contact-form table{
    border: none;
    th,
    td{
      background: transparent;
      color: #636464;
      font-size: 13px;
      line-height: 23px;
      border: none;
    }
    td.column-one{
      width: 25%;
    }
    tr{
      background: transparent;
    }
    tr:hover{
      th,td{
        background: transparent;
      }
    }
    input.dsidx-text{
      width: 90%;
      padding: 5px 10px;
    }
    label{
      font-family: "Roboto",Helvetica,Arial,sans-serif;
      margin-top: 5px;
    }
    input[type=button]{
      padding: 10px 18px;
      font-family: "Roboto",Helvetica,Arial,sans-serif;
      font-size: 14px;
      height: auto;
      line-height: 18px;
    }
    textarea.dsidx-contact-form-comments{
      padding: 5px 10px;
      width: 93%;
    }
    select{
      @include border-radius(0);
    }
  }
}


#dsidx-header{
  background: #fbfbfb;
  border: 1px solid #dedede;
  padding: 15px;
  margin-bottom: 20px;
  table{
    border: none;
    td{
      border: none;
      background: transparent;
    }
  }
  table#dsidx-primary-data{
    th{
      border: none;
      background: transparent;
      color: #636464;
      font-family: "Roboto",Helvetica,Arial,sans-serif;
      font-weight: normal;
    }
    td{
      border: none;
      color: #636464;
      font-family: "Roboto",Helvetica,Arial,sans-serif;
      font-weight: normal;
    }
  }
  .dsidx-secondary-row{
    background: transparent;
  }
}


.sidebar .widget{
  &.dsidx-widget-listings{
    h3.title{
      margin-bottom: 0;
    }
  }
  .dsidx-map{
    height: auto;
    padding: 5px 18px 49px 22px;
    .dsidx-details{
      height: auto;
      margin-bottom: 5px;
    }
  }
  .dsidx-slideshow{
    height: auto;
    ul{
      height: 106px;
      margin: 5px 0;
    }
    .featured-listing{
      height: auto;
      h4{
        background-color: #4dc7ec;
        color: #fff;
        padding: 5px 5px 6px;
        font-family: "Roboto", Helvetica, Arial, sans-serif;
        font-weight: normal;
        text-transform: capitalize;
        margin: 0;
        font-size: 14px;
        a{
          color: #fff;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      img{
        margin: 0 5px 0 0;
        border: none;
      }
    }
  }
  .dsidx-expanded {
    background: white;
    padding: 15px;

    .featured-listing{
      height: auto;
      margin: 0 0 15px;
      border-top: none;
      h4{
        background-color: #4dc7ec;
        color: #fff;
        padding: 5px 5px 6px;
        font-family: "Roboto", Helvetica, Arial, sans-serif;
        font-weight: normal;
        text-transform: capitalize;
        margin: 0;
        font-size: 14px;
        border: none;
        margin-left: 0 !important;
        a{
          color: #394041;
          &:hover{
            color: #dc7d44;
          }
        }
      }
      .dsidx-photo{
        position: relative;
        left: auto;
        top: auto;
        float: none;
      }
      img{
        margin: 0 5px 10px 0;
        border: none;
      }
      ul{
        padding: 0 0;
        clear: both;
        margin: 0;
        margin-left: 0 !important;
      }
      ul > li{
        background: url(../images/icon-arrow-small.png) no-repeat 4px 8px;
        padding: 5px 5px 5px 20px;
        float: none;
        display: block;
        margin: 0;
        font-family: Helvetica, Arial, sans-serif;
        font-size: 12px;
        line-height: 20px;
      }
      ul > li:first-child{
        border-top: none;
      }
    }
  }
  .dsidx-search-widget{
    form{
      padding: 0 20px;
    }
    table{
      border: none;
      background: transparent;
      tr{
        th{
          padding: 8px 6px 8px 8px;
        }
        th,
        td{
          background: transparent;
          border: none;
          position: relative;
        }
      }
    }
    label{
      color: #394041;
      font-size: 13px;
      display: block;
      float: none;
      width: 98%;
      padding: 0;
    }
    span.select-wrapper{
      font-family: "Roboto", Helvetica, Arial, sans-serif;
      position: relative;
      display: block;
      input {
        width: 136px;
        height: 24px;
        padding-right: 30px;
        margin-bottom: 22px;
        cursor: pointer;
        color: #7b8283;
        background: url(../images/dropdown-arrow.png) no-repeat right top;
      }
    }
    select{
      @include border-radius(0);
      border: 1px solid #cdcdcd;
      padding: 5px 6px;
      font-size: 12px;
      margin: 0 auto 10px;
      display: block;
      width: 98% !important;
    }
    select.dsidx-search-widget-propertyTypes{
      display: block;
      margin: 0 auto 10px;
      width: 98% !important;
    }
    input[type="text"], textarea{
      border: 1px solid #cdcdcd;
      padding: 2px 6px;
      font-size: 12px;
      width: 98% !important;
      float: none !important;
      margin-bottom: 10px;
      height: 28px;
    }
    input[type=text].dsidx-price{
      width: 48% !important;
    }
    .dsidx-search-button{
      text-align: center;
      .submit{
        padding: 8px 16px;
        font-family: "Roboto",Helvetica,Arial,sans-serif;
        font-size: 14px;
        text-transform: capitalize;
        color: #fff;
      }
      br{
        display: none;
      }
    }
  }
  .dsidx-widget-single-listing{
    border: none;
    h3.widget-title{
      font-family: "Roboto",Helvetica,Arial,sans-serif;
    }
  }
  .dsidx-controls{
    display: none;
  }
  .dsidx-widget .submit{
    @extend .real-btn;
  }
}

.disable-responsive .sidebar .widget .dsidx-search-widget input[type=text].dsidx-price{
  width: 34%;
}
.disable-responsive .sidebar .widget .dsidx-widget-single-listing{
  width: 220px;
}


