/*  dsIDX search widget for homepage
--------------------------------------------------------*/
#home-idx-search{
  position: relative;
  background-color: #fff;
  border-bottom: 1px solid #dedede;
  h3.home-widget-label{
    background-color:  #394041;
    color: #dedee0;
    font-size: 21px;
    line-height: 33px;
    margin: 0;
    padding: 10px 25px;
    position: absolute;
    top: -53px;
    left: 0;
    text-transform: capitalize;
    z-index: 999;
  }
  .dsidx-search-widget{
    max-width: none;
    padding: 25px;
    background: #fff;
    border-bottom: 1px solid #dedede;
    form{
      margin: 0;
    }
    select{
      width: auto !important;
      float: none;
      margin: 0 20px 15px 0;
      border-radius: 0;
      padding: 5px 10px;
    }
    label{
      width: auto !important;
      margin: 5px 5px 0 0;
      float: none;
      padding: 0;
      vertical-align: top;
      color: $headingsColor;
    }
    input[type=text]{
      width: 125px !important;
      margin: 0 20px 15px 0;
      float: none;
      padding: 2px 4px 2px 8px;;
      box-sizing: inherit;
      &.dsidx-price{
        width: 80px !important;
        margin-right: 5px;
      }
      &.dsidx-improvedsqft,
      &.dsidx-beds,
      &.dsidx-baths{
        width: 120px !important;
      }
      &#idx-q-PriceMax{
        margin-right: 20px;
      }
    }
    .dsidx-search-button{
      display: inline-block;
      margin: 0;
      padding: 0;
      vertical-align: top;
    }
    .dsidx-search-button .submit{
      margin: 0;
      padding: 5px 15px;
      font-size: 14px;
      text-transform: capitalize;
      vertical-align: bottom;
    }
  }
}

#dsidx-search-form-main{
  .dsidx-search-grid{
    float: none;
    width: auto;
  }
}

#home-idx-search .dsidx-resp-search-box {
  padding: 20px 0 20px 30px;
}

#home-idx-search .dsidx-resp-search-form {
  margin-left: -15px;
}

#home-idx-search .dsidx-resp-area {
  width: 172px !important;
  float: left !important;
  margin-left: 15px;
}

.dsidx-resp-area input[type="text"],
.dsidx-resp-area select {
  height: 35px;
}

.dsidx-resp-area select{

}

.dsidx-resp-area-submit input[type="submit"] {
  height: 35px;
  background: #ec894d;
  border: none;
  color: #fff;
}

.dsidx-resp-area-submit input[type="submit"]:hover {
  background: #df5400;
}

.sidebar form {
  margin: 0;
}

.sidebar .dsidx-widget {
  padding: 10px 20px;
}

.sidebar .dsidx-resp-area select {
  margin: 0;
}

.sidebar .dsidx-resp-area {
  margin-bottom: 10px;
}


.sidebar .dsidx-resp-search-box > label {
  display: none;
}

.dsidx-widget-quick-search .dsidx-resp-search-box{
  padding: 10px 20px;
}

.dsidx-resp-area input[type="text"],
.dsidx-resp-area select{
  margin-bottom: 0;
}
// dsIDXpress responsive styles
@media ( min-width: 500px ) and ( max-width: 1199px ) {
  #home-idx-search .dsidx-resp-area {
    width: 208px !important;
  }
}

@media ( min-width: 440px ) and ( max-width: 499px ) {
  #home-idx-search .dsidx-resp-area {
    width: 90% !important;
  }
}

@media ( max-width: 439px ) {
  #home-idx-search .dsidx-resp-area {
    width: 80% !important;
  }
}
